<template>
  <div>
    <NoticeDetail :notice="notice"></NoticeDetail>
    <div style="background-color: #fafafa;height: 10px"></div>
  </div>
</template>
<script>
  import  NoticeDetail from '../components/subViews/NoticeDetail'
  import  user from '../components/subViews/user'
  import api from '@/api/share'
  export default {
    name: "customizationDetails",
    components:{NoticeDetail,api,user},
    data () {
      return {
        notice:{},
      }
    },
    created(){
      this.GetnoticeDetail()
    },
    methods: {
      GetnoticeDetail() {
        api.noticeDetail({
        }).then(res => {
          console.log(res)
          if (res.status == 1) {
            this.notice = res.data.notice
          } else
            this.$toast(res.msg)
        })
      }
    }
  }
</script>

<style scoped>

</style>



