import Vue from 'vue'
import Router from 'vue-router'
import learnClass from '@/components/learnClass' //学习班详情b5
import hotSpotDetails from '@/components/hotSpotDetails'//热点详情b36
import ResearchDetail from '@/components/ResearchDetail'//研究动态详情b63
import InformationDetails from '@/components/InformationDetails'//咨询详情b65
import noticeDetails from '@/components/noticeDetails'//通知详情b29

import openClassDetails from '@/components/openClassDetails'//公开课详情C6
import articleDetails from '@/components/articleDetails'//公开课详情C8
import lessoningDetails from '@/components/lessoningDetails'//直播课详情-付费的C27

import customizationDetails from '@/components/customizationDetails'//定制详情e5
import InformDetails from '@/components/InformDetails'//资讯详情E9
//组件
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/components/InformDetails',
      name: 'InformDetails',
      meta: {
        title: '资讯详情',
      },
      component: InformDetails
    },{
      path: '/components/articleDetails',
      name: 'articleDetails',
      meta: {
        title: '公开课详情',
      },
      component: articleDetails
    },{
      path: '/components/openClassDetails',
      name: 'openClassDetails',
      meta: {
        title: '公开课详情',
      },
      component: openClassDetails
    },{
      path: '/components/customizationDetails',
      name: 'customizationDetails',
      meta: {
        title: '定制详情',
      },
      component: customizationDetails
    },{
      path: '/components/InformationDetails',
      name: 'InformationDetails',
      meta: {
        title: '咨询详情',
      },
      component: InformationDetails
    },{
      path: '/components/ResearchDetail',
      name: 'ResearchDetail',
      meta: {
        title: '研究动态详情',
      },
      component: ResearchDetail
    },{
      path: '/',
      name: 'learnClass',
      meta: {
        title: '学习班详情',
      },
      component: learnClass
    },{
      path: '/components/hotSpotDetails',
      name: 'hotSpotDetails',
      meta: {
        title: '热点详情',
      },
      component: hotSpotDetails
    },{
      path: '/components/lessoningDetails',
      name: 'lessoningDetails',
      meta: {
        title: '直播课详情',
      },
      component: lessoningDetails
    },{
      path: '/components/noticeDetails',
      name: 'noticeDetails',
      meta: {
        title: '通知',
      },
      component: noticeDetails
    }
  ]
})



