<template>
    <div>
      <NoticeDetail :notice="notice"></NoticeDetail>
      <div style="background-color: #fafafa;height: 10px"></div>
      <user :user="user"></user>
    </div>
</template>
<script>
  import  NoticeDetail from '../components/subViews/NoticeDetail'
  import  user from '../components/subViews/user'
  import api from '@/api/share'
    export default {
      name: "noticeDetails",
      components:{NoticeDetail,api,user},
      data () {
        return {
          notice:{},
          user:{},
        }
      },
      created(){
      this.GetnoticeDetail()
      },
      methods: {
        GetnoticeDetail() {
          api.noticeDetail({
          }).then(res => {
            console.log(res)
            if (res.status == 1) {
              this.notice = res.data.notice
              this.user = res.data.user
              console.log('notice',this.notice)
              console.log('user',this.user)
            } else
              this.$toast(res.msg)
          })
        }
      }
    }
</script>

<style scoped>

</style>



